import React from "react";

export const vusername = (value) => {
  var nospace = /^\S*$/;
  if (value.length === 0) return;

  if (value.length < 4 || value.length > 20) {
    return (
      <div className="alert alert-danger" role="alert">
        Prisijungimo vardas turi bti sudarytas i ne maiau kaip 4 simboli.{" "}
      </div>
    );
  } else if (!value.match(nospace)) {
    return (
      <div className="alert alert-danger" role="alert">
        sitikinkite, kad vesdami prisijungimo vard nepalikote tarp.{" "}
      </div>
    );
  }
};

export const vpassword = (value) => {
  var paswd = /^(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])[a-zA-Z0-9].{7,19}$/;

  if (value.length < 8 || value.length > 20 || value.match(paswd) === null) {
    return false;
  } else return true;
};
